.modal {
	z-index: 100;
	position: fixed;
	top: 22vh;
	left: 10%;
	width: 80%;
	background: #f7fff7;
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
	border-radius: 10px 10px 8px 9px;
	text-align: center;
}

.modal__header {
	/*width: 100%;*/
	border-radius: 8px 8px 0 0;
	padding: 1rem 0.5rem;
	background: #1a535c;
	color: #f7fff7;
}

.modal__header h2 {
	margin: 1rem;
}

.modal__content {
	padding: 1rem 1rem;
}

.modal__footer {
	padding: 1rem 1rem;
}

@media (min-width: 768px) {
	.modal {
		left: calc(50% - 20rem);
		width: 40rem;
	}
}

.modal-enter {
	transform: translateY(-10rem);
	opacity: 0;
}

.modal-enter-active {
	transform: translateY(0);
	opacity: 1;
	transition: all 200ms;
}

.modal-exit {
	transform: translateY(0);
	opacity: 1;
}

.modal-exit-active {
	transform: translateY(-10rem);
	opacity: 0;
	transition: all 200ms;
}
